<template>
  <div class="cleared-wrap">
    <div class="cleared-left">
      <left></left>
    </div>
    <div class="cleared-right">
      <div class="cleared-header">
        <div class="left">
          <span class="header-line"></span>
          <span class="header-content"></span>
          <span class="service-num">3</span>
        </div>
        <div class="search-box">
          <input class="input-search" type="text" placeholder="">
          <button class="btn-search" type="button"><i class="iconfont icon-search"></i>&nbsp;&nbsp;</button>
        </div>
      </div>
      <div class="table-box">
        <div class="tab">
          <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="" name="first">
              <ul class="tab-list">
                <li v-for="(item, index) in approvalService" :class="{active:index == num}" @click="tab(index)">{{item}}</li>
              </ul>
              <table>
                <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(item, index) in tableData">
                  <td>{{index+1}}</td>
                  <td>{{item.tableName}}</td>
                  <td>{{item.taskName}}</td>
                  <td>{{item.departmentName}}</td>
                  <td>{{item.applicant}}</td>
                  <td style="width: 350px">
                    <span class="edit">
                      <a class="edit-text" href="#"><i class="iconfont icon-edit"></i>&nbsp;</a>
                    </span>
                    <span class="check">
                      <a class="check-text" href="#"><i class="iconfont icon-check"></i>&nbsp;</a>
                    </span>
                    <span class="flow">
                      <a class="flow-text" href="#"><i class="iconfont icon-flow"></i>&nbsp;</a>
                    </span>
                  </td>
                </tr>
                </tbody>
              </table>
              <div class="pages">
                <el-pagination
                  background
                  layout="prev, pager, next"
                  :total="1000">
                </el-pagination>
              </div>
            </el-tab-pane>
            <el-tab-pane label="" name="second"></el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
  import left from './left'
  export default {
    name: 'cleared',
    data() {
      return {
        activeName: 'first',
        //
        num: 0,
        approvalService: ['', '', '', '', '', '', ''],
        //
        tableData: [
          {tableName: '', taskName: '', departmentName: '', applicant: ''},
          {tableName: '', taskName: '', departmentName: '', applicant: ''},
          {tableName: '', taskName: '', departmentName: '', applicant: ''},
          {tableName: '', taskName: '', departmentName: '', applicant: ''},
          {tableName: '', taskName: '', departmentName: '', applicant: ''}
        ]
      }
    },
    methods: {
      //tab
      handleClick(tab, event) {
        //console.log(tab, event)
      },
      //
      tab (index) {
        this.num = index
      }
    },
    mounted(){
      let dom = document.querySelector('.leftbar-list>.item:nth-child(2)');
      dom.classList.add("active")
    },
    destroyed(){
      let dom = document.querySelector('.leftbar-list>.item:nth-child(2)');
      dom.classList.remove("active")
    },
    components: {
      left
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/lib-variable";
  @import "~assets/css/lib-mixin";
  .cleared-wrap {
    display: flex;
    justify-content: space-between;
    margin-left: 0.2rem;
    width: 100%;
    box-sizing: border-box;
    .cleared-left {
      flex: 1;
      width: 298px;
    }
    .cleared-right {
      width: 100%;
      padding: 30px;
      margin-left: 10px;
      background: #fff;
      border-radius: 5px;
      .cleared-header {
        display: flex;
        justify-content: space-between;
        margin-bottom: 35px;
        .left {
          font-size: 0;
          .header-line {
            display: inline-block;
            height: 18px;
            width: 4px;
            background-image: linear-gradient(0deg,
              #16a4e4 0%,
              #304fea 100%),
            linear-gradient(
                #2789e3,
                #2789e3);
            background-blend-mode: normal,
            normal;
          }
          .header-content {
            display: inline-block;
            margin-left: 6px;
            line-height: 18px;
            font-size: 16px;
            font-weight: 600;
            color: #333333;
            vertical-align: top;
          }
          .service-num {
            display: inline-block;
            vertical-align: top;
            font-size: 14px;
            color: #303030;
            margin-left: 22px;
          }
        }
        .search-box {
          position: relative;
          .input-search {
            width: 330px;
            height: 32px;
            background-color: #f3f3f3;
            border-radius: 16px;
            border: none;
            padding-left: 20px;
          }
          .btn-search {
            position: absolute;
            top: 0;
            right: 0;
            width: 79px;
            height: 32px;
            background-color: #197aff;
            border-radius: 0 16px 16px 0;
            color: #fff;
            outline: none;
            border: 0;
          }
        }
      }
      .table-box {
        .tab {
          .tab-list {
            margin: 20px 0 30px 0;
            li {
              display: inline-block;
              line-height: 32px;
              padding: 0 20px;
              margin-right: 15px;
              border-radius: 16px;
              color: #848484;
              border: 1px solid #bbb;
              cursor: pointer;
              &.active{
                border-color: #197aff;
                background: #f5f7fa;
                color: #197aff;
              }
            }
          }
          table {
            width: 100%;
            font-size: 14px;
            color: #444444;
            border: 1px solid #E0E0E0;
            border-radius: 5px;
            th {
              height: 40px;
              background: #1981F9;
              color: #fff;
              text-align: center;
            }
            tr:nth-child(2n) {
              background: #F5F7FA;
            }
            td {
              height: 40px;
              text-align: center;
              border-left: 1px solid #E0E0E0;
              .edit {
                margin-right: 10px;
                .edit-text {
                  color: #848484;
                  .icon-edit {
                    vertical-align: middle;
                    color: #4996FF;
                  }
                }
              }
              .check {
                margin-right: 10px;
                .check-text {
                  color: #848484;
                  .icon-check {
                    vertical-align: middle;
                    font-size: 28px;
                    color: #197AFF;
                  }
                }
              }
              .flow {
                .flow-text {
                  color: #848484;
                  .icon-flow {
                    vertical-align: middle;
                    font-size: 24px;
                    color: #FCAA3C;
                  }
                }
              }
            }
          }
          .pages {
            margin-top: 20px;
            text-align: right;
          }
        }
      }
    }
  }

</style>



