              .z-bg1{background: url(~assets/img/moreserve.png) no-repeat;background-position:  0rem -1rem ;}
              .d-genre-right{
                flex:1;
                margin-left:0.1rem;
                font-weight: none;
                p{
                  line-height: 0.46rem;
                  color: #464646;
                }
              }
            }
          }

      }
    }

  }
</style>



