<template>
  <div class="cover">
    <el-dialog
        title=""
        :visible.sync="centerDialogVisible"
        width="60%"
        :before-close="close">
        <div class="cover-content">
            <h3></h3>
            <div><strong>1</strong></div>
            <div><strong>2</strong></div>
            <div class="pic-pidding">
                <ul class="clearfix">
                    <li v-for="(list,index) in lists">
                        <img :src="list.imgUrl" width="95" alt="">
                        <span class="dot" v-if="!(index==lists.length - 1)"></span>
                        <span class="title">{{list.text}}</span>
                    </li>
                </ul>
            </div>
            <div><strong>3</strong></div>
            <div>   
                <p>1</p>
                <p>2
             
      </p>
                <p>3</p>
            </div>
        </div>
        <div class="enter-btn">
            <button @click="goon"></button>
        </div>
        
    </el-dialog>
  </div>
</template>

<script>
export default {
   name: 'coverBox',
   props: ['centerDialogVisible'],
   data() {
      return {
          lists: [
              { text:'', imgUrl: require('../../assets/img/cover_01.png')},
              { text:'', imgUrl: require('../../assets/img/cover_02.png')},
              { text:'', imgUrl: require('../../assets/img/cover_03.png')},
              { text:'', imgUrl: require('../../assets/img/cover_04.png')},
              { text:'', imgUrl: require('../../assets/img/cover_05.png')},
              { text:'', imgUrl: require('../../assets/img/cover_06.png')}
          ]
      };
    },
    mounted(){
        console.log(this.centerDialogVisible)
    },
    methods:{
        close(){
            this.$emit('close')
        },
        goon(){
            this.$router.push('/officeHall/fillForm')
        }
    }
}
</script>

<style lang="scss" scoped>
.cover{
    color: #333;
}
.cover-content{
    border-top: 1px solid #e6e6e6;
    h3{
        font-size: 16px;
        color: #333;
        padding: 20px 0 30px;
        font-weight: normal;
    }
    &>div{
        strong{
            color: #000;
        }
        font-size: 14px;
        color: #333;
        p{
           font-size: 14px;
           line-height: 30px;
           padding-left:25px; text-indent:-25px;
        }
        &:last-child{
            margin-top: 15px;
        }
    }
    .pic-pidding{
        padding: 30px 65px 40px 45px;
        ul{
            overflow: hidden;
        }
        li{
            float: left;
            &:nth-child(6){
                .title{
                    margin-left: 0;
                }
            }
        }
        .dot{
            display: inline-block;
            width: 81px;
            height: 95px;
            background: url(../../assets/img/cover_07.png) no-repeat center center;
        }
        .title{
            display: block;
            margin-top: 10px;
            color: #2b73e9;
            font-size: 14px;
            width: 100%;
            text-align: center;
            margin-left: -45px;
        }
    }
}
.enter-btn{
    margin-top: 40px;
    text-align: center;
    button{
        width: 108px;
        height: 38px;
        color: #2b73e9;
        font-size: 14px;
        line-height: 40px;
        border-radius: 20px;
        background: #d1ebfa;
        border: 1px solid #2f52ea;
    }
}
</style>





